@media (max-width: 768px) {
  .landing {
    &-page-header.landing-block {
      justify-content: space-between;
      padding: 10px 10px;
      background: #373737;

    }
    &-language-button {
      position: static;
    }
    &-nav {
      display: none;
      width: auto;
      &-container {
        max-width: fit-content;
        margin: 0;
      }
      &_opened {
        padding: 15px 10px;
        position: absolute;
        display: flex;
        flex-direction: column;
        top: 49px;
        right: -10px;
        width: 100vw;
        background: #373737;
        z-index: 10;
      }
      &-icon {
        display: block;
        width: 32px;
        height: 32px;
      }
    }
    &-voice {
      &-command {
        width: 90%;
        margin-right: 0;
      }
    }
    &-contact {
      margin: 12px 0;
    }
  }
}

.rotate-left {
  &>svg {
    transform: rotate(45deg);
  }
}

.rotate-left:hover {
  &>svg {
    transform: rotate(0deg);
  }
}
.rotate-right {
  &>svg {
    transform: rotate(-45deg);
  }
}

.rotate-right:hover {
  &>svg {
    transform: rotate(0deg);
  }
}
.stop-hand {
  >svg>g>path  {
    transition: opacity 0.5s linear;
  }
}

.stop-hand:hover {
  &>svg>g>path {
    opacity: 0;
  }
}